.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  margin: 0;
  padding: 0;

}
body{
  background-color: pink;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.header{
  text-align: center;
  color: green;
  
}
.app{
  
  background: linear-gradient(90deg,#3a72ec 0%, rgba(197,255,0,0.91) 80%);
  width: 100%;
  height: 200px;
  padding: 30px;
  border-radius: 10px;
  display: flex;
  justify-content: space-evenly;
}
.score-section{
  display: flex;
  align-items: center;
  font-size: 30px;
  color: blue;
}
.question-section{
  width: 100%;
  position: relative;
}
.question-count{
  margin-bottom: 20px;
  font-size: 22px;

}
.question-count span{
  font-size: 30px;
}
.question-text{
  font-size: 20px;
  font-weight: bold;
}
.answer-section{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
button{
  display: flex;
  justify-content: flex-start;
  border-radius: 5px;
  padding: 10px;
  width: 80%;
  cursor: pointer;
  font-size: 20px;
  background-color: pink;
}
